    fill: #ffffff;
  }
  .player__button:hover .player__button--icon-replay,
  .player__button:hover .player__button--icon-cc,
  .player__button:hover .player__button--icon-fullscreen,
  .player__button:focus .player__button--icon-replay,
  .player__button:focus .player__button--icon-cc,
  .player__button:focus .player__button--icon-fullscreen {
    fill: #ffffff;
    opacity: 0.6;
  }
  .player__button:active .player__button--icon-replay,
  .player__button:active .player__button--icon-cc,
  .player__button:active .player__button--icon-fullscreen {
    opacity: 0.3;
  }
  .player__replay {
    margin-left: 4.5%;
  }
  .player__full-screen {
    margin-right: 5.3%;
  }
